// Import sprintf-js
const vsprintf = require( 'sprintf-js' ).vsprintf;

// eslint-disable-next-line import/no-unresolved
import $ from 'jquery';

import Field from '../field/field.js';
import FieldsGroup from '../field/group.js';
import FieldsRepeater from '../field/repeater.js';

import RecipeTypeSelector from '../recipe/type-selector.js';

import Tokens from '../tokens/tokens.js';

import Modal from './modal.js';

/**
 * Creates the data required for Automator to render
 *
 * @param {Function} callback The function to invoke after the data is ready
 * @return {undefined}
 */
export const createDataOnLoad = ( callback = '' ) => {
	// Output if the developer mode is enabled
	if ( UncannyAutomator.dev.developerMode ) {
		consoleLogLabel( 'Start creating the main object' );
	}

	// Check if there is a callback defined
	// eslint-disable-next-line no-empty-function
	callback = ! isEmpty( callback ) ? callback : () => {};

	/**
	 * Completes the process of UI creation once it knows what's the
	 * recipe type the user wants to create
	 *
	 * @return {undefined}
	 */
	const completeProcess = () => {
		// Filter Automator items
		filterAutomatorItemsByRecipeType();
		// Group the triggers and actions by integration
		groupItemsByIntegration();

		// Get recipe items on page load
		recipeObject( 'create', {
			allRecipes: UncannyAutomator.recipes_object,
		} );

		// Remove unnecessary data
		// delete UncannyAutomator.recipes_object;
		delete UncannyAutomator.triggers;
		delete UncannyAutomator.actions;

		// Output if the developer mode is enabled
		if ( UncannyAutomator.dev.developerMode ) {
			consoleLogLabel( 'Ended creating the main object' );
		}

		// Invoke the callback
		callback();
	};

	// Check if WP is ready
	if ( ! isEmpty( UncannyAutomator.recipe.type ) ) {
		// Complete the process
		completeProcess();
	} else {
		// Call to Recipe Type Selector
		new RecipeTypeSelector( () => {
			// Complete the process
			completeProcess();
		} );
	}
};

/**
 * Updates all the objects used for the UI using the recipes_object object
 *
 * @param {String} action     Action to perform. Valid actions: 'create', 'delete',
 * @param {Object} attributes Data, check each switch to see the required data
 * @return {Boolean}            True if the action was made successfully
 */
export const recipeObject = ( action = null, attributes = {} ) => {
	// Create response variable and set default value
	let response = false;

	// Define valid actions
	const validActions = [ 'create', 'validate' ];

	// Check if the action is not undefined and if it's a valid action
	if ( isDefined( action ) && inArray( action, validActions ) ) {
		switch ( action ) {
			case 'create':
				// Define required attributes properties
				const requiredData = [ 'allRecipes' ];

				// Check if the attributes object has all the required properties
				if ( inArray( requiredData, Object.keys( attributes ) ) ) {
					// Get this recipe in the allRecipes object
					let recipe;

					// Create allRecipes variable
					const allRecipes = attributes.allRecipes;

					$.each( allRecipes, ( index, theRecipe ) => {
						if ( theRecipe.ID == UncannyAutomator.recipe.id ) {
							recipe = theRecipe;
						}
					} );

					// Store extra options in the main UncannyAutomator object for future use
					if ( isDefined( recipe.extra_options ) && ! isEmpty( recipe.extra_options ) ) {
						UncannyAutomator.recipes_object[ recipe.ID ].extra_options = recipe.extra_options;
					}

					// Add element to all triggers, actions and closures to define the itemType. We're going to need this later
					$.each( recipe.triggers, ( index, item ) => ( item.itemType = 'trigger' ) );
					$.each( recipe.actions, ( index, item ) => ( item.itemType = 'action' ) );
					$.each( recipe.closures, ( index, item ) => ( item.itemType = 'closure' ) );

					// Merge all the recipe triggers, actions and closures into one array
					const items = [].concat.apply( [], [ recipe.triggers, recipe.actions, recipe.closures ] );

					// Reset items object
					itemsObject( 'reset' );

					// Check if the recipe is anonymous
					const recipeIsAnonymous = UncannyAutomator.recipe.type == 'anonymous';

					// Iterate each item and add it to the itemsObject
					$.each( items, ( index, item ) => {
						// We're going to get all the required information to before adding the item to the items object
						let itemToAdd;

						// Get the integration ID
						const itemIntegration = item.meta.integration;

						// Get the item code in this variable
						const itemCode = item.meta.code;

						let extraOptions;

						try {
							extraOptions =
								UncannyAutomator.recipes_object[ recipe.ID ].extra_options[ itemIntegration ][
									itemCode
								];
						} catch ( error ) {
							// There are no extra options for this item, do nothing
						}

						// Get the data from the global item
						const globalItem = getGlobalItem( itemIntegration, item.itemType, itemCode );

						// Check if the required PHP code for this item exists
						if ( isDefined( globalItem ) ) {
							// Create variable to save all the options
							const options = {};

							if ( ! isEmpty( extraOptions ) ) {
								// If extra options were supplied, they will be used instead of the gloab ones
								if ( isDefined( extraOptions.options ) ) {
									$.each( extraOptions.options, ( i, option ) => {
										options[ option.option_code ] = [ option ];
									} );
								}

								if ( isDefined( extraOptions.options_group ) ) {
									$.extend( true, options, extraOptions.options_group );
								}
							} else {
								// Check if it has simple options (options that aren't part of a group of options)
								if ( isDefined( globalItem.options ) ) {
									$.each( globalItem.options, ( i, option ) => {
										options[ option.option_code ] = [ option ];
									} );
								}

								// Add group of options
								if ( isDefined( globalItem.options_group ) ) {
									$.extend( true, options, globalItem.options_group );
								}
							}

							// We're going to create another options variable to create the options object as we're going to add it to the item object
							const groupsOfOptions = {};

							$.each( options, ( groupKey, fields ) => {
								const allFieldsOnThisGroup = [];

								// Check if it has more this group of fields has more than one option
								const hasMoreThanOneOption = fields.length > 1;

								$.each( fields, ( i, field ) => {
									/**
									 * Sanitizes the field data
									 *
									 * @param {Object} field The field data
									 * @return {Object}      The field data, sanitized
									 */

									// eslint-disable-next-line require-jsdoc, no-shadow
									const sanitizeFieldData = ( field ) => {
										// Create field object
										const fieldObject = {
											isGroup: hasMoreThanOneOption,
											itemId: item.ID,
											itemType: item.itemType,

											isRequired: isDefined( field.required ) ? field.required : false,
											isReadOnly: isDefined( field.read_only ) ? field.read_only : false,
											supportsTinyMCE: isDefined( field.supports_tinymce )
												? field.supports_tinymce
												: false,
											supportsCopyToClipboard: isDefined( field.copy_to_clipboard )
												? field.copy_to_clipboard
												: false,

											isAjax: isDefined( field.is_ajax ) ? field.is_ajax : false,

											groupId: groupKey,
											optionCode: field.option_code,
											fieldType: field.input_type,

											label: field.label,
											description: field.description,
											placeholder: field.placeholder,
											isHidden: isDefined( field.is_hidden ) ? field.is_hidden : false,
											tokenName: isDefined( field.token_name ) ? field.token_name : field.label,

											defaultValue: field.default_value,
											currentValue: isDefined( item.meta[ field.option_code ] )
												? item.meta[ field.option_code ]
												: field.default_value,
											customFieldTokens: isDefined( field.relevant_tokens )
												? field.relevant_tokens
												: false,
											relevantTokens: isDefined( field.relevant_tokens )
												? field.relevant_tokens
												: false,

											maxLength: isDefined( field.max_length ) ? field.max_length : null,
										};

										/**
										 * Check for type-specific options
										 */
										if ( fieldObject.fieldType == 'date' ) {
											fieldObject.dateFormat = isDefined( field.date_format )
												? field.date_format
												: 'Y-m-d';
											fieldObject.dateAltFormat = isDefined( field.date_alt_format )
												? field.date_alt_format
												: UncannyAutomator.format.date;

											// Check if it supports a range
											fieldObject.supportsRange = isDefined( field.supports_range )
												? field.supports_range
												: false;

											// Check if it has min date defined
											if ( isDefined( field.min_date ) ) {
												fieldObject.minDate = field.min_date;
											}

											// Check if it has max date defined
											if ( isDefined( field.max_date ) ) {
												fieldObject.maxDate = field.max_date;
											}
										}

										if ( fieldObject.fieldType == 'time' ) {
											fieldObject.timeFormat = isDefined( field.time_format )
												? field.time_format
												: 'h:i K';
											fieldObject.timeAltFormat = isDefined( field.time_alt_format )
												? field.time_alt_format
												: 'h:i K';

											// Check if it has min time defined
											if ( isDefined( field.min_time ) ) {
												fieldObject.minTime = field.min_time;
											}

											// Check if it has max date defined
											if ( isDefined( field.max_date ) ) {
												fieldObject.maxTime = field.max_time;
											}
										}

										/**
										 * We want to make supportsTokens true by
										 * default, but we have to check if the field
										 * should support tokens first.
										 *
										 * - Field type: For now, the token selector
										 * only supports text, textarea, integer, float,
										 * email, and URL fields. Check if this field
										 * is one of those.
										 *
										 * - Item type: The tokens are rendered only in
										 * the user selector, actions and closures.
										 * Check if the item type if one of those.
										 *
										 * - Read-only property: Check if the field is
										 * not read-only. Even if the field meets the
										 * previous conditions, it doesn't make sense
										 * to render the token selector on a field
										 * which value is supposed to be unmodifiable.
										 *
										 * If the field meets all the previous
										 * conditions, check if it has a defined value
										 * for the supportsTokens property. Otherwise,
										 * set it true. If it doesn't meet at least
										 * one of the conditions, make it false.
										 */

										if (
											[
												'text',
												'textarea',
												'int',
												'float',
												'email',
												'url',
												'select',
												'date',
												'time',
											].includes( fieldObject.fieldType ) &&
											[ 'action', 'closure' ].includes( fieldObject.itemType ) &&
											! fieldObject.isReadOnly
										) {
											fieldObject.supportsTokens = isDefined( field.supports_tokens )
												? field.supports_tokens
												: true;
										} else {
											fieldObject.supportsTokens = false;
										}

										// If it's a select, check if it support custom values
										if ( fieldObject.fieldType == 'select' ) {
											// Check if the supportsCustomValue parameter is defined. If it's not,
											// then check if the field is being used on an action or closure. If so, make it true
											// by default, or false otherwise. Then, on "Field", set the default value to false.
											// We're doing this because, for now, we're supporting custom values on dropdowns
											// only on actions and closures, nowhere else. So if someone tries to use a dropdown
											// outside an action or closure, it shouldn't render the "custom value" option by default
											if ( isDefined( field.supports_custom_value ) ) {
												fieldObject.supportsCustomValue = field.supports_custom_value;
											} else if ( [ 'action', 'closure' ].includes( fieldObject.itemType ) ) {
												fieldObject.supportsCustomValue = true;
											} else {
												fieldObject.supportsCustomValue = false;
											}

											// Check if it supports custom values and it also supports tokens
											if ( fieldObject.supportsCustomValue ) {
												// Add custom value
												fieldObject.customValue = isDefined(
													item.meta[ `${ fieldObject.optionCode }_custom` ]
												)
													? item.meta[ `${ fieldObject.optionCode }_custom` ]
													: '';
											}

											// Check if it has a description for the custom value field
											if ( isDefined( field.custom_value_description ) ) {
												fieldObject.customValueDescription = field.custom_value_description;
											}

											// Check if it supports multiple values
											if ( isDefined( field.supports_multiple_values ) ) {
												fieldObject.supportsMultipleValues = field.supports_multiple_values;
											}
										}

										// Check if it an AJAX field
										if ( fieldObject.isAjax ) {
											fieldObject.targetField = isDefined( field.fill_values_in )
												? field.fill_values_in
												: '';
											fieldObject.endpoint = isDefined( field.endpoint ) ? field.endpoint : '';
											fieldObject.integrationId = globalItem.integration;

											// Check if it's an ajax field without target field
											// This means no other field will change when this one changes
											// and that we have to do an AJAX request when rendering the field, on page load
											if ( isEmpty( fieldObject.targetField ) ) {
												fieldObject.targetField = field.option_code;
											}
										}

										// Check if it's select or radio. In that case, add the options
										if ( [ 'select', 'radio' ].includes( fieldObject.fieldType ) ) {
											fieldObject.options = isDefined( field.options ) ? field.options : [];
										}

										// Return field object
										return fieldObject;
									};

									let fieldObject, fieldInstance;

									// Check if it's a group of fields
									if ( field.input_type == 'group' ) {
										// Get the field data
										fieldObject = {
											groupId: groupKey,
											optionCode: field.option_code,
											fieldType: field.input_type,

											isGroup: hasMoreThanOneOption,
											itemId: item.ID,
											itemType: item.itemType,

											label: field.label,
											description: field.description,

											tokenName: isDefined( field.token_name ) ? field.token_name : field.label,

											isRequired: isDefined( field.required ) ? field.required : false,

											fields: field.fields.map( ( fieldData ) => {
												return sanitizeFieldData( fieldData );
											} ),
										};

										// Create field instance
										fieldInstance = new FieldsGroup( fieldObject );
									} else if ( field.input_type == 'repeater' ) {
										// Try to parse the value of the repeater
										try {
											item.meta[ field.option_code ] = JSON.parse(
												item.meta[ field.option_code ]
											);
										} catch ( e ) {
											// Continue regardless of error

											// Output error if the developer mode is enabled
											if ( UncannyAutomator.dev.developerMode ) {
												// eslint-disable-next-line no-console
												console.warning( e );
											}
										}

										// Get field data
										fieldObject = {
											groupId: groupKey,
											optionCode: field.option_code,
											fieldType: field.input_type,

											isGroup: hasMoreThanOneOption,
											itemId: item.ID,
											itemType: item.itemType,

											label: field.label,
											description: field.description,

											tokenName: isDefined( field.token_name ) ? field.token_name : field.label,

											defaultValue: field.default_value,
											currentValue: isDefined( item.meta[ field.option_code ] )
												? item.meta[ field.option_code ]
												: field.default_value,

											isRequired: isDefined( field.required ) ? field.required : false,

											hideActions: isDefined( field.hide_actions ) ? field.hide_actions : false,

											fields: field.fields.map( ( fieldData ) => {
												return sanitizeFieldData( fieldData );
											} ),
										};

										// Check if this one has a custom "Add row" label
										if ( isDefined( field.add_row_button ) ) {
											fieldObject.addRowButtonLabel = field.add_row_button;
										}

										// Check if this one has a custom "Remove row" label
										if ( isDefined( field.remove_row_button ) ) {
											fieldObject.removeRowButtonLabel = field.remove_row_button;
										}

										// Create field instance
										fieldInstance = new FieldsRepeater( fieldObject );
									} else {
										// Otherwise, treat it as a normal field

										// Get the field data
										fieldObject = sanitizeFieldData( field );

										// Create the field instance
										fieldInstance = new Field( fieldObject.fieldType, fieldObject, false );
									}

									// Add field to the group of fields
									allFieldsOnThisGroup.push( fieldInstance );
								} );

								// Array where we're going to save the buttons of this item
								const buttons = [];

								// Check if this group of options has buttons
								if ( isDefined( globalItem.buttons ) ) {
									// Iterate buttons
									$.each( globalItem.buttons, ( i, button ) => {
										// Check if we have to add the button to this group
										if ( button.show_in == groupKey ) {
											buttons.push( button );
										}
									} );
								}

								// Push fields to the groupsOfOptions object
								groupsOfOptions[ groupKey ] = {
									fields: allFieldsOnThisGroup,
									isValid: false, // FALSE by default, but we're going to do a validation later
									buttons: buttons,
								};
							} );

							// Create object with the item data
							itemToAdd = {
								id: item.ID,
								itemType: item.itemType,
								itemCode: itemCode,
								integrationCode: globalItem.integration,
								isLive: item.post_status == 'publish' ? true : false,
								options: groupsOfOptions,
								meta: item.meta,
								isMissing: false,
								tokens: isDefined( item.tokens ) ? item.tokens : [],
								inlineCss: isDefined( globalItem.inline_css ) ? globalItem.inline_css : '',
								filterTokens: isDefined( globalItem.filter_tokens ) ? globalItem.filter_tokens : '',
							};

							// Check if the support link is defined
							if ( isDefined( globalItem.support ) ) {
								itemToAdd.support = globalItem.support;
							}

							// Create meta for those field that don't have a value saved in the database but have a default value

							// Get all fields
							const fields = {};

							// Create an object with all the options from the field
							$.each( groupsOfOptions, ( optionKey, opts ) => {
								$.each( opts.fields, ( i, field ) => {
									fields[ field.getAttributes().optionCode ] = field;
								} );
							} );

							$.each( fields, ( i, field ) => {
								const fieldAttributes = field.getAttributes();
								if (
									( ! isDefined( itemToAdd.meta[ fieldAttributes.optionCode ] ) ||
										isEmpty( itemToAdd.meta[ fieldAttributes.optionCode ] ) ) &&
									! isEmpty( fieldAttributes.currentValue )
								) {
									itemToAdd.meta[ fieldAttributes.optionCode ] = fieldAttributes.currentValue;
								}
							} );

							// If it's anonymous, add the "canLogUserIn" property
							// Also set the default value
							if ( recipeIsAnonymous ) {
								itemToAdd.canLogNewUserIn = isDefined( globalItem.can_log_in_new_user )
									? globalItem.can_log_in_new_user
									: true;
							}
						} else {
							// Create object with the item data
							itemToAdd = {
								id: item.ID,
								itemType: item.itemType,
								itemCode: item.meta.code,
								integrationCode: item.meta.integration,
								integrationName: item.meta.integration_name,
								sentence: item.meta.sentence,
								isLive: item.post_status == 'publish' ? true : false,
								tokens: [],
								options: {},
								meta: item.meta,
								isMissing: true,
							};

							// If it's anonymous, add the "canLogUserIn" property
							// Also set the default value
							if ( recipeIsAnonymous ) {
								itemToAdd.canLogNewUserIn = isDefined( item.meta.can_log_in_new_user )
									? item.meta.can_log_in_new_user == 'true'
									: true;
							}
						}

						// We have to add it
						itemsObject( 'add', itemToAdd );
					} );

					// Check if the recipe is anonymous and can log the new user in
					if ( recipeIsAnonymous ) {
						// Set the default value
						UncannyAutomator.recipe.canLogNewUserIn = false;

						// Get the recipe items
						const recipeTriggers = itemsObject( 'get', {
							filter: {
								itemType: 'trigger',
							},
						} );

						// Check if it has items
						if ( Object.keys( recipeTriggers ) > 0 ) {
							// Change the default value
							UncannyAutomator.recipe.canLogNewUserIn = true;

							// Iterate the triggers
							$.each( recipeTriggers, ( index, trigger ) => {
								// Check if this trigger allows the recipe
								// to log the new user in
								UncannyAutomator.recipe.canLogNewUserIn =
									UncannyAutomator.recipe.canLogNewUserIn && trigger.canLogNewUserIn;
							} );
						}
					}

					// Check if we should validate the data
					if (
						! isDefined( attributes.validate ) ||
						( isDefined( attributes.validate ) && attributes.validate )
					) {
						// Validate the recipe to update the isValid elements
						recipeObject( 'validate' );
					}
				} else {
					// Show to the developer what attributes are missing
					const missingAttributes = JSON.stringify( arrayDifference( attributes, requiredData ) );

					if ( UncannyAutomator.dev.developerMode ) {
						// eslint-disable-next-line no-console
						console.error(
							vsprintf( '%s: Some attributes are missing: %s', [ 'recipeObject', missingAttributes ] )
						);
					}
				}

				break;

			case 'validate':
				// Valid by default, will be changed to false if we find an error
				let isRecipeValid = true;

				// Check if the recipe has triggers, actions and/or closures
				// Update UncannyAutomator.recipe.hasLive
				$.each( UncannyAutomator.recipe.hasLive, ( itemType ) => {
					// All false
					UncannyAutomator.recipe.hasLive[ itemType ] = false;
				} );

				// Check if there is at least one item of that type in the main object
				$.each( UncannyAutomator.recipe.items, ( itemID, itemData ) => {
					// If at least one live item, then set TRUE
					if ( itemData.isLive ) {
						UncannyAutomator.recipe.hasLive[ itemData.itemType ] = true;
					}
				} );

				// Iterate each item
				$.each( UncannyAutomator.recipe.items, ( itemID, itemData ) => {
					// We will use this variable to decide if the item is valid or not
					let itemIsValid = true;

					// Iterate each group of fields
					$.each( itemData.options, ( optionsGroupID, optionsGroupData ) => {
						// We will use this variable to decide if the group of fields is valid or not
						let groupIsValid = true;

						// Iterate each field
						$.each( optionsGroupData.fields, ( index, field ) => {
							// Validate the field. Read validateField comments to learn more.
							const fieldValidation = field.validate( false );

							// Check if the option is invalid, if at least one field is invalid make all the group invalid
							if ( ! fieldValidation.isValid ) {
								groupIsValid = false;
							}
						} );

						// Set result
						UncannyAutomator.recipe.items[ itemID ].options[ optionsGroupID ].isValid = groupIsValid;

						// Check if the group is invalid, if at least on group of fields is invalid make all the item invalid
						if ( ! groupIsValid ) {
							itemIsValid = false;
						}
					} );

					UncannyAutomator.recipe.items[ itemID ].isValid = itemIsValid;

					// Check if the item is invalid. If at least one item is invalid make all the recipe invalid
					if ( itemData.isLive && ! itemIsValid ) {
						isRecipeValid = false;
					}
				} );

				// Now check what items we have in the object
				// We must have at least one trigger and an action or closure
				// Check if it has at least one trigger
				if ( ! UncannyAutomator.recipe.hasLive.trigger ) {
					isRecipeValid = false;

					// Set error message
					UncannyAutomator.recipe.message.error = UncannyAutomator.i18n.validation.recipe.oneTrigger;
				} else {
					// Validate user selector
					// eslint-disable-next-line no-lonely-if
					if (
						// Check if it's an anonymous recipe
						UncannyAutomator.recipe.type == 'anonymous' &&
						// if it requires user data (thus it needs the user selector)
						UncannyAutomator.recipe.requiresUserData &&
						// and if the user selector is not valid (doesn't have required data)
						! UncannyAutomator.recipe.userSelector.isValid
					) {
						// Then the recipe is not valid
						isRecipeValid = false;

						// Add message
						UncannyAutomator.recipe.message.error = UncannyAutomator.i18n.validation.recipe.userSelector;
					} else if (
						// Check if it has at least one action or closure
						! UncannyAutomator.recipe.hasLive.action &&
						! UncannyAutomator.recipe.hasLive.closure
					) {
						isRecipeValid = false;

						// Set error message
						UncannyAutomator.recipe.message.error = UncannyAutomator.i18n.validation.recipe.oneAction;
					} else if (
						! UncannyAutomator.recipe.hasLive.trigger &&
						! UncannyAutomator.recipe.hasLive.action &&
						! UncannyAutomator.recipe.hasLive.closure
					) {
						// Set error message
						UncannyAutomator.recipe.message.error = UncannyAutomator.i18n.validation.recipe.liveItems;
					}
				}

				UncannyAutomator.recipe.isValid = isRecipeValid;

				// Reset message if recipe is valid
				if ( isRecipeValid ) {
					// Reset message, we don't need it
					UncannyAutomator.recipe.message.error = '';
				}

				// Everything is ok, return true
				response = true;

				break;
		}
	} else if ( UncannyAutomator.dev.developerMode ) {
		// eslint-disable-next-line no-console
		console.error( vsprintf( '%s: The action "%s" isn\'t defined', [ 'recipeObject', action ] ) );
	}

	return response;
};

/**
 * Add, update, delete, update, validate and clean the main recipe object
 *
 * @param {String}  action              Action to perform. Valid actions: 'add', 'update', 'delete', 'reset', 'get', and 'clean'.
 * @param {Object}  attributes          The attributes
 * @param {Integer} attributes.id       Item ID
 * @param {String}  attributes.itemType The item type. The valid types are 'trigger', 'action' and 'closure'
 * @return {Boolean}                       True if the action was made successfully
 */
export const itemsObject = ( action = null, attributes = {} ) => {
	// Create response variable and set default value
	let response = false;

	// Define valid actions
	const validActions = [ 'add', 'delete', 'clean', 'reset', 'update', 'get' ];

	// Check if the action is not undefined and if it's a valid action
	if ( isDefined( action ) && inArray( action, validActions ) ) {
		switch ( action ) {
			case 'add':
				// Extend default attributes object with default values
				attributes = $.extend(
					{
						isValid: false, // Because it's going to be empty
						isLive: false, // Draft by default
					},
					attributes
				);

				// Define required attributes properties
				const requiredData = [ 'id', 'itemType' ];

				// Check if the attributes object has all the required properties
				if ( inArray( requiredData, Object.keys( attributes ) ) ) {
					// Has ID, check if it wasn't added
					if ( ! isDefined( UncannyAutomator.recipe.items[ attributes.id ] ) ) {
						// Create property
						UncannyAutomator.recipe.items[ attributes.id ] = attributes;

						// Clean undefined properties
						itemsObject( 'clean', {} );

						// Everything is ok, return true
						response = true;
					}
				} else {
					// Show to the developer what attributes are missing
					const missingAttributes = JSON.stringify( arrayDifference( attributes, requiredData ) );

					if ( UncannyAutomator.dev.developerMode ) {
						// eslint-disable-next-line no-console
						console.error(
							vsprintf( '%s: Some attributes are missing: %s', [ 'itemsObject', missingAttributes ] )
						);
					}
				}

				break;

			case 'delete':
				attributes = $.extend(
					{
						isValid: false,
					},
					attributes
				);

				if ( ! isEmpty( attributes.id ) ) {
					if ( isDefined( UncannyAutomator.recipe.items[ attributes.id ] ) ) {
						delete UncannyAutomator.recipe.items[ attributes.id ];

						// Everything is ok, return true
						response = true;
					} else if ( UncannyAutomator.dev.developerMode ) {
						// eslint-disable-next-line no-console
						console.error(
							vsprintf( "%s: We didn't find an item with this the ID %s", [
								'itemsObject',
								attributes.id,
							] )
						);
					}
				} else if ( UncannyAutomator.dev.developerMode ) {
					// eslint-disable-next-line no-console
					console.error( vsprintf( '%s: Invalid item ID', [ 'itemsObject' ] ) );
				}

				break;

			case 'clean':
				// Delete undefined and null properties

				const items = {};

				$.each( UncannyAutomator.recipe.items, ( index, value ) => {
					if ( isDefined( value ) ) {
						items[ index ] = value;
					}
				} );

				UncannyAutomator.recipe.items = items;

				// Everything is ok, return true
				response = true;

				break;

			case 'update':
				// Update item attributes

				if ( ! isEmpty( attributes.id ) ) {
					if ( isDefined( UncannyAutomator.recipe.items[ attributes.id ] ) ) {
						// Extend item object properties with new ones
						UncannyAutomator.recipe.items[ attributes.id ] = $.extend(
							{},
							UncannyAutomator.recipe.items[ attributes.id ],
							attributes
						);

						// Everything is ok, return true
						response = true;
					} else if ( UncannyAutomator.dev.developerMode ) {
						// eslint-disable-next-line no-console
						console.error(
							vsprintf( "%s: We didn't find an item with this the ID %s", [
								'itemsObject',
								attributes.id,
							] )
						);
					}
				} else if ( UncannyAutomator.dev.developerMode ) {
					// eslint-disable-next-line no-console
					console.error( vsprintf( '%s: Invalid item ID', [ 'itemsObject' ] ) );
				}

				break;

			case 'reset':
				// Delete all the items in the items object
				UncannyAutomator.recipe.items = {};

				// Return true
				response = true;
				break;

			case 'get':
				// Check if we have to filter items
				if ( ! isEmpty( attributes.filter ) ) {
					// Create a new items object. We're going to add items to this one if the filters match
					// eslint-disable-next-line no-shadow
					const items = {};
					// Iterate all current items
					$.each( UncannyAutomator.recipe.items, ( itemId, item ) => {
						let matchFilters = true;

						$.each( attributes.filter, ( key, value ) => {
							if ( item[ key ] != value ) {
								matchFilters = false;
							}
						} );

						// Check if we have to add this item
						if ( matchFilters ) {
							items[ itemId ] = item;
						}
					} );

					// Return the new items object
					response = items;
				} else {
					response = UncannyAutomator.recipe.items;
				}

				break;
		}
	} else if ( UncannyAutomator.dev.developerMode ) {
		// eslint-disable-next-line no-console
		console.error( vsprintf( '%s: The action "%s" isn\'t defined', [ 'itemsObject', action ] ) );
	}

	return response;
};

/**
 * Add the triggers and actions to each integration.
 * Change the data structure so it's possible to know
 * what items and integrations are pro
 *
 * @return {undefined}
 */
export const groupItemsByIntegration = () => {
	// Iterate the integrations
	// eslint-disable-next-line no-unused-vars
	Object.entries( UncannyAutomator.integrations ).forEach( ( [ integrationID, integration ] ) => {
		// Get the list of available triggers and actions
		// Sort them alphabetically
		const triggers = {
				available: sortArrayOfObjectsAlphabetically(
					UncannyAutomator.triggers.filter( ( item ) => item.integration == integrationID ),
					'select_option_name',
					( sentence ) => replaceCurlyBraces( sentence )
				),
				unavailable: [],
			},
			actions = {
				available: sortArrayOfObjectsAlphabetically(
					UncannyAutomator.actions.filter( ( item ) => item.integration == integrationID ),
					'select_option_name',
					( sentence ) => replaceCurlyBraces( sentence )
				),
				unavailable: [],
			};

		if ( ! isDefined( UncannyAutomator.wp ) || ! UncannyAutomator.wp ) {
			// Check if there are PRO items for this integration
			if (
				isDefined( UncannyAutomator.proFeatures ) &&
				isDefined( UncannyAutomator.proFeatures[ integrationID ] )
			) {
				// Get the recipe type
				let recipeType = UncannyAutomator.recipe.type;
				// Just for filtering the triggers, change "user" to "logged-in"
				recipeType = recipeType == 'user' ? 'logged-in' : recipeType;

				// Push the pro triggers to the unavailable triggers array
				// Sort them alphabetically
				triggers.unavailable = sortArrayOfObjectsAlphabetically(
					// Elements to sort alphabetically
					UncannyAutomator.proFeatures[ integrationID ].triggers
						// Filter by recipe type
						// trigger.type is the trigger time
						.filter( ( trigger ) => trigger.type == recipeType ),
					// Sort them by name
					'name',
					// Remove the curly braces before comparing the names
					( sentence ) => replaceCurlyBraces( sentence )
				);

				// Push the pro actions to the unavailable actions array
				// Sort them alphabetically
				actions.unavailable = sortArrayOfObjectsAlphabetically(
					UncannyAutomator.proFeatures[ integrationID ].actions,
					'name',
					( sentence ) => replaceCurlyBraces( sentence )
				);
			}
		}

		// Evaluate if all the available items are pro
		// To do that, iterate every element using the method every
		// and return the value of "is_pro"
		triggers.allItemsArePro = triggers.available.every( ( item ) =>
			isDefined( item.is_pro ) ? item.is_pro : false
		);
		actions.allItemsArePro = actions.available.every( ( item ) =>
			isDefined( item.is_pro ) ? item.is_pro : false
		);

		// Check if the integration has available triggers and actions
		triggers.hasAvailableItems = triggers.available.length > 0;
		actions.hasAvailableItems = actions.available.length > 0;

		// Check if the integration has unavailable triggers and actions
		triggers.hasUnavailableItems = triggers.unavailable.length > 0;
		actions.hasUnavailableItems = actions.unavailable.length > 0;

		// Check if it has any kind of items
		triggers.hasTriggers = triggers.hasAvailableItems || triggers.unavailable.length > 0;
		actions.hasActions = actions.hasAvailableItems || actions.unavailable.length > 0;

		// Add the items to the integration
		UncannyAutomator.integrations[ integrationID ].items = {
			triggers: triggers,
			actions: actions,
		};
	} );
};

/**
 * Sorts an array of objects using one of the properties
 *
 * @param {Array}    items             The array
 * @param {String}   propertyToCompare The property used to compare the elements
 * @param {Function} filter            A custom function to compare the elements
 * @return {Array}                       The array, sorted
 */
export const sortArrayOfObjectsAlphabetically = ( items = [], propertyToCompare = '', filter ) => {
	// Check if there isn't a filter defined
	if ( ! isDefined( filter ) ) {
		filter = ( value ) => value;
	}

	// Check if it's an array and it's not empty
	if ( Array.isArray( items ) && ! isEmpty( items ) ) {
		// Sort the array of integrations using the integration name
		items = items.sort( ( a, b ) => {
			// Define the default value
			let compare = 0;

			// Check if the elements have the properties we're going to use to compare
			if ( isDefined( a[ propertyToCompare ] ) && isDefined( b[ propertyToCompare ] ) ) {
				// Filter the values
				const valueA = filter( a[ propertyToCompare ] ),
					valueB = filter( b[ propertyToCompare ] );

				// Check which integration name should go first
				compare = valueA.localeCompare( valueB );

				// Validate the result
				// Do NOT rely on exact return values of -1 or 1
				// Negative and positive integer results vary between browsers (as well as between browser versions) because the W3C specification only mandates negative and positive values. Some browsers may return -2 or 2, or even some other negative or positive value.
				compare = compare > 0 ? 1 : compare;
				compare = compare < 0 ? -1 : compare;
			}

			// Return result
			return compare;
		} );
	}

	// Return the new array
	return items;
};

/**
 * Returns a list of triggers or actions by integration
 *
 * @param {String} integrationID The integration ID
 * @param {String} itemType      The item type. Valid values: "trigger", "triggers", "action" and "actions"
 * @param {Object} options       The options
 * @return {Object}                And object with the available and unavailable items
 */
export const getItemsByIntegration = ( integrationID = '', itemType = 'triggers', options ) => {
	// Extend the options
	options = Object.assign(
		{
			addDeprecatedItems: false,
		},
		options
	);

	// Create the array where we're going to save the items
	let items = {
		available: {},
		unavailable: {},
	};

	// Convert itemType "trigger" to "triggers"
	itemType = itemType == 'trigger' ? 'triggers' : itemType;
	// Convert itemType "action" to "actions"
	itemType = itemType == 'action' ? 'actions' : itemType;

	// Validate the parameter itemType
	if ( [ 'triggers', 'actions' ].includes( itemType ) ) {
		// Check if the integration ID is defined
		if ( ! isEmpty( integrationID ) ) {
			// Check if the integration exists
			if ( isDefined( UncannyAutomator.integrations[ integrationID ] ) ) {
				// Get the requested items
				items = $.extend( true, {}, UncannyAutomator.integrations[ integrationID ].items[ itemType ] );

				// Check if we have to remove the deprecated items
				if ( ! options.addDeprecatedItems ) {
					// Remove deprecated items
					items = filterDeprecatedItems( items );
				}
			}
		}
	}

	// Return the triggers
	return items;
};

/**
 * Returns the data about a trigger or action using the item code
 *
 * @param {String} integrationID The integration ID
 * @param {String} itemType      The item type. Vlaid values: "trigger", "triggers", "action" and "actions"
 * @param {String} itemCode      The item code
 * @param {Object} options       Options
 * @return {Object}               Data about the item
 */
export const getItemByCode = ( integrationID = '', itemType = 'triggers', itemCode = '', options = {} ) => {
	// Default response
	let item;

	// Extend the options
	options = Object.assign(
		{
			addDeprecatedItems: false,
		},
		options
	);

	// Convert itemType "trigger" to "triggers"
	itemType = itemType == 'trigger' ? 'triggers' : itemType;
	// Convert itemType "action" to "actions"
	itemType = itemType == 'action' ? 'actions' : itemType;

	// Validate the parameter itemType
	if ( [ 'triggers', 'actions' ].includes( itemType ) ) {
		// Check if the integration ID is defined
		if ( ! isEmpty( integrationID ) ) {
			// Get the integration items
			const integrationItems = getItemsByIntegration( integrationID, itemType, {
				addDeprecatedItems: options.addDeprecatedItems,
			} );

			// Get the available items
			const items = integrationItems.available;

			// Search the item
			items.forEach( ( integrationItem ) => {
				// Check the item code
				if ( integrationItem.code == itemCode ) {
					// Save it
					item = integrationItem;
				}
			} );
		}
	}

	return item;
};

/**
 * Removes deprecated items from an items object
 *
 * @param {Object} items Items object
 * @return {Object}        Items object, without deprecated items
 */
export const filterDeprecatedItems = ( items ) => {
	// Remove deprecated items
	items.available = items.available.filter( ( item ) => ! item.is_deprecated );

	// Check if there are still available items
	items.hasAvailableItems = items.available.length > 0;

	// Check if it still has triggers or actions
	if ( isDefined( items.hasTriggers ) ) {
		items.hasTriggers = items.hasAvailableItems || items.unavailable.length > 0;
	} else if ( isDefined( items.hasActions ) ) {
		items.hasActions = items.hasAvailableItems || items.unavailable.length > 0;
	}

	// Check if all the available items are pro now
	items.allItemsArePro = items.available.every( ( item ) => ( isDefined( item.is_pro ) ? item.is_pro : false ) );

	return items;
};

/**
 * Updates the user selector data
 *
 * @param {String} action     Action to perform. Valid actions: 'set-step', 'get-step', 'set-source', 'update-fields', 'reset-fields'.
 * @param {Object} attributes The attributes
 * @return {Boolean}                       True if the action was made successfully
 */
export const userSelectorObject = ( action = null, attributes = {} ) => {
	// Create response variable and set default value
	let response = false;

	// Define valid actions
	const validActions = [ 'set-step', 'get-step', 'set-source', 'update-fields', 'reset-fields' ];

	// Create shortcut of the user selector object
	// const userSelector = UncannyAutomator.recipe.userSelector;

	// Check if the action is not undefined and if it's a valid action
	if ( isDefined( action ) && inArray( action, validActions ) ) {
		// Create empty array to check if the actions have the required data
		let requiredData = [];

		switch ( action ) {
			/**
			 * Get the current step so we know what we have to render
			 * There are 4 steps:
			 *
			 * 1. select-source: Where the recipe creator select the source of the user.
			 *                   The options are "Existing user" and "New user".
			 * 2. set-options: Where the recipe creator defines values of the fields.
			 * 3. show-sentence: When the data is complete and valid.
			 */

			case 'set-step':
				// Define required attributes properties
				requiredData = [ 'step' ];

				// Check if the attributes object has all the required properties
				if ( inArray( requiredData, Object.keys( attributes ) ) ) {
					// Set status
					UncannyAutomator.recipe.userSelector.step = attributes.step;
				} else {
					// Show to the developer what attributes are missing
					const missingAttributes = JSON.stringify( arrayDifference( attributes, requiredData ) );

					if ( UncannyAutomator.dev.developerMode ) {
						// eslint-disable-next-line no-console
						console.error(
							vsprintf( '%s: Some attributes are missing: %s', [
								'userSelectorObject',
								missingAttributes,
							] )
						);
					}
				}

				break;

			case 'get-step':
				// Create the variable we're going to return
				// Set default value
				let step = 'select-source';

				// Check if the step property is defined
				if ( isDefined( UncannyAutomator.recipe.userSelector.step ) ) {
					// If it is, then return that value
					step = UncannyAutomator.recipe.userSelector.step;
				} else if (
					// Try to get step
					// Check if there are fields saved
					! isEmpty(
						UncannyAutomator.recipe.userSelector.data[ UncannyAutomator.recipe.userSelector.source ]
					)
				) {
					// Show sentence
					step = 'show-sentence';

					// If we have fields it's because we already valid them before,
					// thus the user selector is valid
					UncannyAutomator.recipe.userSelector.isValid = true;
				}

				// Return step
				response = step;

				break;

			case 'set-source':
				// Define required attributes properties
				requiredData = [ 'source' ];

				// Check if the attributes object has all the required properties
				if ( inArray( requiredData, Object.keys( attributes ) ) ) {
					// Set status
					UncannyAutomator.recipe.userSelector.source = attributes.source;
				} else {
					// Show to the developer what attributes are missing
					const missingAttributes = JSON.stringify( arrayDifference( attributes, requiredData ) );

					if ( UncannyAutomator.dev.developerMode ) {
						// eslint-disable-next-line no-console
						console.error(
							vsprintf( '%s: Some attributes are missing: %s', [
								'userSelectorObject',
								missingAttributes,
							] )
						);
					}
				}

				break;

			case 'update-fields':
				// Define required attributes properties
				requiredData = [ 'source', 'data' ];

				// Check if the attributes object has all the required properties
				if ( inArray( requiredData, Object.keys( attributes ) ) ) {
					// Check if the source parameter is valid
					if ( [ 'existingUser', 'newUser' ].includes( attributes.source ) ) {
						// Check if we already have fields
						if ( isEmpty( UncannyAutomator.recipe.userSelector.data ) ) {
							UncannyAutomator.recipe.userSelector.data = {
								existingUser: {},
								newUser: {},
							};
						}

						// Update fields
						UncannyAutomator.recipe.userSelector.data[ attributes.source ] = $.extend(
							{},
							UncannyAutomator.recipe.userSelector.data[ attributes.source ],
							attributes.data
						);
					} else if ( UncannyAutomator.dev.developerMode ) {
						// eslint-disable-next-line no-console
						console.error(
							vsprintf( '%s: The source parameter is invalid. Valid values are: %s', [
								'userSelectorObject',
								[ 'existingUser', 'newUser' ],
							] )
						);
					}
				} else {
					// Show to the developer what attributes are missing
					const missingAttributes = JSON.stringify( arrayDifference( attributes, requiredData ) );

					if ( UncannyAutomator.dev.developerMode ) {
						// eslint-disable-next-line no-console
						console.error(
							vsprintf( '%s: Some attributes are missing: %s', [
								'userSelectorObject',
								missingAttributes,
							] )
						);
					}
				}

				break;

			case 'reset-fields':
				// Return boolean
				UncannyAutomator.recipe.userSelector.data = {
					existingUser: {},
					newUser: {},
				};
				break;
		}
	} else if ( UncannyAutomator.dev.developerMode ) {
		// eslint-disable-next-line no-console
		console.error( vsprintf( '%s: The action "%s" isn\'t defined', [ 'userSelectorObject', action ] ) );
	}

	return response;
};

/**
 * Filters triggers, actions and closures by recipe type.
 * This won't filter the items of a recipe, but the available
 * items in Automator. This means that the filtered items won't
 * be available after.
 *
 * @return {undefined}
 */
export const filterAutomatorItemsByRecipeType = () => {
	// Check if the type attribute has a valid value
	if ( [ 'user', 'anonymous' ].includes( UncannyAutomator.recipe.type ) ) {
		// Output if the developer mode is enabled
		if ( UncannyAutomator.dev.developerMode ) {
			// Start group
			// eslint-disable-next-line no-console
			consoleLogLabel( 'Filter Automator items', true, {
				collapseGroup: true,
			} );

			// Show current recipe type
			// eslint-disable-next-line no-console
			console.log( vsprintf( 'Current recipe type: %s', [ UncannyAutomator.recipe.type ] ) );
		}

		// Filter triggers
		UncannyAutomator.triggers = UncannyAutomator.triggers.filter( ( trigger ) => {
			// Output if the developer mode is enabled
			if ( UncannyAutomator.dev.developerMode ) {
				// Show trigger
				// eslint-disable-next-line no-console
				console.log( trigger );

				// Show decision
				if ( trigger.type == UncannyAutomator.recipe.type ) {
					consoleLogResult( 'success', 'Keep' );
				} else {
					consoleLogResult( 'error', 'Remove' );
				}
			}

			// Check if it matches the recipe type
			return trigger.type == UncannyAutomator.recipe.type;
		} );

		// Check if debug mode is enabled
		if ( UncannyAutomator.dev.developerMode ) {
			// Start group
			// eslint-disable-next-line no-console
			console.groupEnd();
		}
	}
};

/**
 * Performs a REST Call
 *
 * @param {String}   endPoint  Endpoint of the REST call
 * @param {Object}   data      Data to be sent in the call
 * @param {Function} onSuccess Function to be invoked if the call is successful
 * @param {Function} onFail    Function to be invoked if the call fails
 * @param {Object}   options   The parameters sent in the call
 * @return {undefined}
 */
export const restCall = ( endPoint = null, data = null, onSuccess = null, onFail = null, options = {} ) => {
	// Define the valid endpoints
	const validEndpoints = [
		'add',
		'delete',
		'update',
		'change_post_status',
		'recipe_completions_allowed',
		'recipe_max_completions_allowed',
		'change_post_title',
		'change_post_recipe_type',
		'user-selector',
		'set_recipe_terms',
		'schedule_action',
		'remove_schedule',
		'set_recipe_requires_user',
	];

	// Check custom options
	options = $.extend(
		{},
		{
			requiresRecipesObject: true,
			renderUI: true,
		},
		options
	);

	// Check if the endPoint parameter is a valid endpoint
	if ( inArray( endPoint, validEndpoints ) ) {
		// Do AJAX
		$.ajax( {
			method: 'POST',
			url: `${ UncannyAutomator.restURL }/${ endPoint }/`,
			// eslint-disable-next-line camelcase
			data: `${ $.param( data ) }&${ $.param( { doing_rest: 1 } ) }`,
			dataType: 'json',

			/**
			 * Attachs Nonce the the header of the request
			 *
			 * @param {Object} xhr jqXHR
			 * @return {undefined}
			 */
			beforeSend: ( xhr ) => {
				xhr.setRequestHeader( 'X-WP-Nonce', UncannyAutomator.nonce );
			},

			// eslint-disable-next-line require-jsdoc
			success: ( response ) => {
				// Output if the developer mode is enabled
				if ( UncannyAutomator.dev.developerMode ) {
					consoleLogLabel( 'REST call ✅', true, {
						collapseGroup: true,
					} );
					// eslint-disable-next-line no-console
					console.log( `Endpoint: ${ endPoint }` );
					// eslint-disable-next-line no-console
					console.log( 'Data:' );
					// eslint-disable-next-line no-console
					console.log( data );
					// eslint-disable-next-line no-console
					console.log( 'Response:' );
					// eslint-disable-next-line no-console
					console.log( response );
					// eslint-disable-next-line no-console
					console.groupEnd();
				}

				// Check if the request succeeded
				if ( response.success ) {
					// Check if we have the new recipes_object, otherwise the data will change on the server but not in the UI
					if ( isDefined( response.recipes_object ) || ! options.requiresRecipesObject ) {
						// Check if onSuccess
						if ( isDefined( onSuccess ) ) {
							// Check if we have to render the UI again
							if ( options.renderUI ) {
								recipeObject( 'create', {
									allRecipes: response.recipes_object,
								} );
							}

							// Invoke callback
							onSuccess( response );
						}
					} else {
						// The call was successful, but the new recipes_object is missing
						if ( UncannyAutomator.dev.developerMode ) {
							// eslint-disable-next-line no-console
							console.error( 'The call was successful, but the new recipes_object is missing.' );
						}

						// Check if the onFail callback is defined
						if ( isDefined( onFail ) ) {
							// Invoke callback
							onFail( response );
						}
					}
				} else {
					// The call was successful, but there were errors
					if ( UncannyAutomator.dev.developerMode ) {
						// eslint-disable-next-line no-console
						console.error( 'The call was successful, but there were errors.' );
					}

					// Check if the onFail callback is defined
					if ( isDefined( onFail ) ) {
						// Invoke callback
						onFail( response );
					}
				}
			},

			statusCode: {
				// eslint-disable-next-line require-jsdoc
				403: ( response ) => {
					// Show console messages if debug is enabled
					if ( UncannyAutomator.dev.developerMode ) {
						// eslint-disable-next-line no-console
						consoleLogLabel( 'REST call 🔴', true, {
							collapseGroup: true,
						} );
						// eslint-disable-next-line no-console
						console.log( 'Data:' );
						// eslint-disable-next-line no-console
						console.log( data );
						// eslint-disable-next-line no-console
						console.log( 'Response' );
						// eslint-disable-next-line no-console
						console.log( '%c403: Access denied', 'color: red' );
						// eslint-disable-next-line no-console
						console.groupEnd();
					}

					if ( isDefined( onFail ) ) {
						onFail( response );
					}
				},
			},

			// eslint-disable-next-line require-jsdoc
			error: ( jqXHR, textStatus, errorThrown ) => {
				// Show console messages if debug is enabled
				if ( UncannyAutomator.dev.developerMode ) {
					// eslint-disable-next-line no-console
					consoleLogLabel( 'REST call 🔴', true, {
						collapseGroup: true,
					} );
					// eslint-disable-next-line no-console
					console.log( 'Data:' );
					// eslint-disable-next-line no-console
					console.log( data );
					// eslint-disable-next-line no-console
					console.log( 'Response' );
					// eslint-disable-next-line no-console
					console.log( '%cUnknown error', 'color: red' );
					// eslint-disable-next-line no-console
					console.log( jqXHR );
					// eslint-disable-next-line no-console
					console.log( textStatus );
					// eslint-disable-next-line no-console
					console.log( errorThrown );
					// eslint-disable-next-line no-console
					console.groupEnd();
				}

				if ( isDefined( onFail ) ) {
					onFail( {
						success: false,
					} );
				}
			},

			// eslint-disable-next-line require-jsdoc
			fail: ( response ) => {
				// Show console messages if debug is enabled
				if ( UncannyAutomator.dev.developerMode ) {
					// eslint-disable-next-line no-console
					consoleLogLabel( 'REST call 🔴', true, {
						collapseGroup: true,
					} );
					// eslint-disable-next-line no-console
					console.log( 'Data:' );
					// eslint-disable-next-line no-console
					console.log( data );
					// eslint-disable-next-line no-console
					console.log( 'Response' );
					// eslint-disable-next-line no-console
					console.log( '%cUnknown error', 'color: red' );
					// eslint-disable-next-line no-console
					console.groupEnd();
				}

				if ( isDefined( onFail ) ) {
					onFail( response );
				}
			},
		} );
	} else if ( UncannyAutomator.dev.developerMode ) {
		// eslint-disable-next-line no-console
		console.error(
			vsprintf( 'The %s endPoint does not exists. The valid endpoints are %s', [
				endPoint,
				validEndpoints.join( ', ' ),
			] )
		);
	}
};

/**
 * Performs an AJAX request
 *
 * @param {String}   action    The ID of the action
 * @param {Object}   data      Data to be sent in the request
 * @param {Function} onSuccess Function to be invoked if the request is successful
 * @param {Function} onFail    Function to be invoked if the request fails
 * @return {undefined}
 */
export const AJAXRequest = ( action = null, data = null, onSuccess = null, onFail = null ) => {
	// Check that {action} is defined
	if ( isDefined( action ) ) {
		// Add {action} to the data object
		data = $.extend( true, data, {
			action: action,
			nonce: UncannyAutomator.nonce,
		} );

		// Do AJAX
		$.ajax( {
			method: 'POST',
			dataType: 'json',
			url: ajaxurl,
			data: data,

			// eslint-disable-next-line require-jsdoc
			success: ( response ) => {
				// Show console messages if debug is enabled
				if ( UncannyAutomator.dev.developerMode ) {
					// eslint-disable-next-line no-console
					consoleLogLabel( 'AJAX call ✅', true, {
						collapseGroup: true,
					} );
					// eslint-disable-next-line no-console
					console.log( 'Data:' );
					// eslint-disable-next-line no-console
					console.log( data );
					// eslint-disable-next-line no-console
					console.log( 'Response' );
					// eslint-disable-next-line no-console
					console.log( response );
					// eslint-disable-next-line no-console
					console.groupEnd();
				}

				// Reload page if user authentication fails
				if ( isDefined( response.status ) && response.status == 'auth-failed' ) {
					location.reload();
				} else if ( isDefined( onSuccess ) ) {
					// Invoke callback
					onSuccess( response, data );
				}
			},

			statusCode: {
				// eslint-disable-next-line require-jsdoc
				403: ( response ) => {
					// Show console messages if debug is enabled
					if ( UncannyAutomator.dev.developerMode ) {
						// eslint-disable-next-line no-console
						consoleLogLabel( 'AJAX call 🔴', true, {
							collapseGroup: true,
						} );
						// eslint-disable-next-line no-console
						console.log( 'Data:' );
						// eslint-disable-next-line no-console
						console.log( data );
						// eslint-disable-next-line no-console
						console.log( 'Response' );
						// eslint-disable-next-line no-console
						consoleLogLabel( '403: Access denied', false, { bgColor: '#ff0000' } );
						// eslint-disable-next-line no-console
						console.groupEnd();
					}

					if ( isDefined( onFail ) ) {
						onFail( response );
					}

					// Check if the onFail callback is defined
					if ( isDefined( onFail ) ) {
						// Invoke callback
						onFail( response );
					}
				},
			},

			// eslint-disable-next-line require-jsdoc
			fail: ( response ) => {
				// Show console messages if debug is enabled
				if ( UncannyAutomator.dev.developerMode ) {
					// eslint-disable-next-line no-console
					consoleLogLabel( 'AJAX call 🔴', true, {
						collapseGroup: true,
					} );
					// eslint-disable-next-line no-console
					console.log( 'Data:' );
					// eslint-disable-next-line no-console
					console.log( data );
					// eslint-disable-next-line no-console
					console.log( 'Response' );
					// eslint-disable-next-line no-console
					console.log( '%cUnknown error', 'color: red' );
					// eslint-disable-next-line no-console
					console.groupEnd();
				}

				if ( isDefined( onFail ) ) {
					onFail( response );
				}
			},
		} );
	} else if ( UncannyAutomator.dev.developerMode ) {
		// eslint-disable-next-line no-console
		console.error( 'We cannot do this AJAX request. {action} is not defined.' );
	}
};

/**
 * Returns all the current value of the field, even if they aren't saved on the database
 * This will use .val() to get the values
 *
 * @param {Integer} itemId The ID of the item
 * @return {Object}         The fields value
 */
export const getCurrentFieldsValues = ( itemId ) => {
	const values = {};

	// Iterate the items' group of fields
	$.each( UncannyAutomator.recipe.items[ itemId ].options, ( index, groupOfFields ) => {
		// Iterate each field
		$.each( groupOfFields.fields, ( i, field ) => {
			// Get value
			const fieldId = field.getFieldID();
			let fieldValue = field.getValue();

			// Check if it isn't defined. In that case get the current value from the currentValue attribute
			if ( ! isDefined( fieldValue ) ) {
				fieldValue = field.getAttributes().currentValue;
			}

			// Check if the field is a select with custom value
			if ( field.getAttributes().fieldType == 'select' && fieldValue == 'automator_custom_value' ) {
				values[ `${ fieldId }_custom` ] = field.getSelectCustomValue();
			}

			// Save value
			values[ fieldId ] = fieldValue;

			// Check if it's a select and also get the readable value
			if ( ! isEmpty( fieldValue ) && field.getAttributes().fieldType == 'select' ) {
				// Get and save the readable value
				values[ `${ fieldId }_readable` ] = field.getReadableValue();
			}
		} );
	} );

	return values;
};

/**
 * Returns a Field instance given a item and field ID
 *
 * @param {Integer} itemId  The item ID
 * @param {String}  fieldId The field ID
 * @return {Field}            The Field instance
 */
export const getField = ( itemId, fieldId ) => {
	let field;

	$.each( UncannyAutomator.recipe.items[ itemId ].options, ( optionsGroupId, optionsGroup ) => {
		$.each( optionsGroup.fields, ( index, fieldInstance ) => {
			if ( fieldId == fieldInstance.getFieldID() ) {
				field = fieldInstance;
			}
		} );
	} );

	return field;
};

/**
 * Returns data about a trigger, action or closure
 *
 * @param {Integer} integrationID The item ID
 * @param {String}  itemType      The item type. Valid values are trigger, action and closure
 * @param {String}  itemCode      The item code
 * @return {Object}                Item's data
 */
export const getGlobalItem = ( integrationID = '', itemType = '', itemCode = '' ) => {
	let theItem;

	// Check if all are defined
	if ( ! isEmpty( integrationID ) && ! isEmpty( itemType ) && ! isEmpty( itemCode ) ) {
		let integrationItems = {};

		// Check if the we're trying to get a trigger or action
		if ( itemType == 'trigger' || itemType == 'action' ) {
			// Check if the integration exists
			if ( isDefined( UncannyAutomator.integrations[ integrationID ] ) ) {
				// Create a reference to the integration
				const integration = UncannyAutomator.integrations[ integrationID ];

				// Check if the items are defined
				if ( isDefined( integration.items ) ) {
					// Set integrationItems
					integrationItems =
						itemType == 'trigger'
							? integration.items.triggers.available
							: integration.items.actions.available;
				}
			}
		} else if ( itemType == 'closure' ) {
			integrationItems = UncannyAutomator.closures;
		}

		// Search for the item
		$.each( integrationItems, ( index, item ) => {
			if ( item.code == itemCode ) {
				// Save the item
				theItem = item;

				// Return false in the loop callback to break the loop
				return false;
			}
		} );
	}

	// Return the item
	return theItem;
};

/**
 * Sets the recipe "requires user" value
 *
 * @param {Boolean}  requires  TRUE if the recipe requires a user
 * @param {Function} onSuccess Callback, on success
 * @param {Function} onFail    Callback, on fail
 * @return {undefined}
 */
export const recipeSetRequiresUser = ( requires = false, onSuccess, onFail ) => {
	restCall(
		'set_recipe_requires_user',
		{
			recipePostID: UncannyAutomator.recipe.id,
			requiresUser: requires,
		},
		( response ) => {
			// Check if the call was successful
			if ( response.success ) {
				// Check if the onSuccess callback is defined
				if ( isDefined( onSuccess ) ) {
					// Update the global "requiresData"
					UncannyAutomator.recipe.requiresUserData = requires;

					// Get the User Selector
					const $userSelector = getDOMElements( 'user-selector' );
					// Show it
					$userSelector.addClass( 'user-selector--show' );

					onSuccess( response );
				}
			}
		},
		( response ) => {
			if ( UncannyAutomator.dev.developerMode ) {
				// eslint-disable-next-line no-console
				console.log( "Couldn't set the recipe requires user status" );
			}

			// Check if the onFail callback is defined
			if ( isDefined( onFail ) ) {
				onFail( response );
			}
		}
	);
};

/**
 * Returns DOM elements, such as a metabox, item of optionGroup
 * In Automator DOM elements are deleted and created in each step, so the reference to a DOM element change.
 *
 * @param {String} element    Code of the element that you want to get
 * @param {Object} attributes Contains required data to get the elements. Depends of the "element" value. Check the variable requiredData in each case.
 * @return {Object}             Object with DOM elements. The properties depend of "element"
 */
export const getDOMElements = ( element, attributes = {} ) => {
	// Create response object
	let response = {};

	// Define valid actions
	const validElements = [ 'metabox', 'items', 'item', 'optionGroup', 'lastItem', 'user-selector' ];

	// Check if the action is not undefined and if it's a valid action
	if ( isDefined( element ) && inArray( element, validElements ) ) {
		// Create array that will store all the requires attributes properties based on the requested element
		let requiredData = [];

		switch ( element ) {
			case 'metabox':
				// Extend default attributes object with default values
				attributes = $.extend( {}, attributes );

				// Define required attributes properties
				requiredData = [ 'type' ];

				// Check if the attributes object has all the required properties
				if ( inArray( requiredData, Object.keys( attributes ) ) ) {
					attributes.cssSelectorMetabox = '';

					// Check the itemType
					if ( attributes.type == 'trigger' ) {
						attributes.cssSelectorMetabox = '#recipe-triggers-ui';
					} else if ( attributes.type == 'action' ) {
						attributes.cssSelectorMetabox = '#recipe-actions-ui';
					}

					const $metabox = $( attributes.cssSelectorMetabox );

					response = $metabox;
				} else {
					// Show to the developer what attributes are missing
					const missingAttributes = JSON.stringify( arrayDifference( attributes, requiredData ) );

					if ( UncannyAutomator.dev.developerMode ) {
						// eslint-disable-next-line no-console
						console.error(
							vsprintf( '%s: Some attributes are missing: %s', [ 'itemsObject', missingAttributes ] )
						);
					}
				}

				break;

			case 'items':
				// Extend default attributes object with default values
				attributes = $.extend( {}, attributes );

				// Define required attributes properties
				requiredData = [ 'type' ];

				// Check if the attributes object has all the required properties
				if ( inArray( requiredData, Object.keys( attributes ) ) ) {
					const metabox = getDOMElements( 'metabox', {
						type: attributes.type,
					} );

					// Get jQuery DOM element
					const $items = metabox.find(
						'.item:not(.item--user-selector):not(.item--start):not(.item--closure)'
					);

					// Add element to the response
					response = $items;
				} else {
					// Show to the developer what attributes are missing
					const missingAttributes = JSON.stringify( arrayDifference( attributes, requiredData ) );

					if ( UncannyAutomator.dev.developerMode ) {
						// eslint-disable-next-line no-console
						console.error(
							vsprintf( '%s: Some attributes are missing: %s', [ 'itemsObject', missingAttributes ] )
						);
					}
				}

				break;

			case 'item':
				// Extend default attributes object with default values
				attributes = $.extend( {}, attributes );

				// Define required attributes properties
				requiredData = [ 'id' ];

				// Check if the attributes object has all the required properties
				if ( inArray( requiredData, Object.keys( attributes ) ) ) {
					// Define CSS Selector
					attributes.cssSelector = `.uap .item[data-id="${ attributes.id }"]`;

					// Get jQuery DOM element
					const $item = $( attributes.cssSelector ),
						$contentContainer = $item.find( '.item__content' );

					// Add element to the response
					response.item = $item;
					response.contentContainer = $contentContainer;
				} else {
					// Show to the developer what attributes are missing
					const missingAttributes = JSON.stringify( arrayDifference( attributes, requiredData ) );

					if ( UncannyAutomator.dev.developerMode ) {
						// eslint-disable-next-line no-console
						console.error(
							vsprintf( '%s: Some attributes are missing: %s', [ 'itemsObject', missingAttributes ] )
						);
					}
				}

				break;

			case 'optionGroup':
				// Extend default attributes object with default values
				attributes = $.extend( {}, attributes );

				// Define required attributes properties
				requiredData = [ 'itemID', 'groupID' ];

				// Check if the attributes object has all the required properties
				if ( inArray( requiredData, Object.keys( attributes ) ) ) {
					// Get main object
					const item = getDOMElements( 'item', { id: attributes.itemID } ).item;

					// Define CSS Selector
					attributes.cssSelectorTokenInSentence = `.item-title__token[data-token-id="${ attributes.groupID }"]`;
					attributes.cssSelectorFieldsContainer = `.item-options__group[data-optiongroup="${ attributes.groupID }"]`;

					const $tokenInSentence = item.find( attributes.cssSelectorTokenInSentence ),
						$fieldsContainer = item.find( attributes.cssSelectorFieldsContainer ),
						$optionsContainer = item.find( '.item-options' );

					response.tokenInSentence = $tokenInSentence;
					response.fieldsContainer = $fieldsContainer;
					response.optionsContainer = $optionsContainer;
				} else {
					// Show to the developer what attributes are missing
					const missingAttributes = JSON.stringify( arrayDifference( attributes, requiredData ) );

					if ( UncannyAutomator.dev.developerMode ) {
						// eslint-disable-next-line no-console
						console.error(
							vsprintf( '%s: Some attributes are missing: %s', [ 'itemsObject', missingAttributes ] )
						);
					}
				}

				break;

			case 'lastItem':
				// Extend default attributes object with default values
				attributes = $.extend( {}, attributes );

				// Define required attributes properties
				requiredData = [ 'type' ];

				// Check if the attributes object has all the required properties
				if ( inArray( requiredData, Object.keys( attributes ) ) ) {
					// Get metabox
					const metabox = getDOMElements( 'metabox', {
						type: attributes.type,
					} );

					// Get last item
					const lastItem = metabox
						.find( '.item:not(.item--user-selector):not(.item--start):not(.item--closure)' )
						.last();

					response = lastItem;
				} else {
					// Show to the developer what attributes are missing
					const missingAttributes = JSON.stringify( arrayDifference( attributes, requiredData ) );

					if ( UncannyAutomator.dev.developerMode ) {
						// eslint-disable-next-line no-console
						console.error(
							vsprintf( '%s: Some attributes are missing: %s', [ 'itemsObject', missingAttributes ] )
						);
					}
				}

				break;

			case 'user-selector':
				// Get user selector container
				response = $( '.user-selector' );

				break;
		}
	} else if ( UncannyAutomator.dev.developerMode ) {
		// eslint-disable-next-line no-console
		console.error( vsprintf( '%s: The element "%s" isn\'t defined', [ 'getDOMElements', element ] ) );
	}

	return response;
};

/**
 * Returns the integration icon, both URL and node
 *
 * @param {String} integrationID The integration ID
 * @return {Object}               Object with the image URL and node
 */
export const getIntegrationIcon = ( integrationID = '' ) => {
	// Default response
	const response = {
		url: '',
		node: null,
	};

	// Get the integration data
	const integration = UncannyAutomator.integrations[ integrationID ];

	// Check if the integration exists
	if ( isDefined( integration ) ) {
		// Create img element
		response.node = document.createElement( 'img' );

		// Check if it has a svg image
		if ( isDefined( integration.icon_svg ) ) {
			// Get the URL
			response.url = integration.icon_svg;

			// Add source to the img element
			response.node.setAttribute( 'src', integration.icon_svg );

			// Add alt
			response.node.setAttribute( 'alt', integration.name );
		} else if ( isDefined( integration.icon_32 ) && isDefined( integration.icon_64 ) ) {
			// Get the URLs
			response.url = {
				icon16: integration.icon_16,
				icon32: integration.icon_32,
				icon64: integration.icon_64,
			};

			// Add source to the img element
			response.node.setAttribute( 'src', integration.icon_32 );

			response.node.setAttribute(
				'srcset',
				vsprintf( '%s 1x, %s 2x', [ integration.icon_32, integration.icon_64 ] )
			);

			// Add alt
			response.node.setAttribute( 'alt', integration.name );
		}
	}

	return response;
};

